.product {
  height: 27vw;
  position: relative;
  color: #111111;
  cursor: pointer;
  z-index: 3; }
  .product:hover .product__image img {
    transform: scale(1.1); }
  .product:hover .product__desc {
    transform: translate3d(0, 0, 0);
    opacity: 1; }
  .product__image {
    display: block;
    height: 100%;
    overflow: hidden; }
    .product__image img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center center;
      transition: 0.4s ease; }
  .product__desc {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    background-color: #ffffff;
    padding: 10px 15px;
    font-size: 14px;
    transform: translate3d(0, 50%, 0);
    transition: 0.4s ease-in-out;
    opacity: 0; }
  .product__name {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 7px; }
  .product__line {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 7px; }
    .product__line img {
      opacity: 0;
      pointer-events: none; }
  .product__price {
    font-size: 18px;
    font-weight: 700; }
  @media screen and (max-width: 992px) {
    .product {
      height: auto; } }
  @media screen and (max-width: 576px) {
