      margin-bottom: 15px; } }

.btn {
  padding: 12px 25px;
  background-color: #DC143C;
  border-radius: 5px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  transition: 0.3s;
  font-size: 16px;
  line-height: 1; }
  .btn:hover {
    background-color: #F08080; }

.basket-unit {
  width: 60px;
  height: 60px;
  border-radius: 5px;
  background-color: #F08080;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 15px;
  right: 50px;
  z-index: 101; }
  .basket-unit.active {
    animation: fly_icon .5s ease-in-out;
    animation-delay: 0.6s; }
  .basket-unit img {
    max-width: 37px; }
  .basket-unit__count {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: #ffffff;
    font-size: 14px; }

.flying-img {
  position: absolute;
  animation: fly_to_cart 1s ease-in-out;
  opacity: 0; }

@keyframes fly_to_cart {
  0% {
    left: 0;
    top: 0;
    opacity: 1; }
  45%, 50% {
    left: var(--left);
    top: 60px;
    opacity: 1; }
  80%, 90% {
    left: var(--left);
    top: var(--top);
    transform: scale(0.2);
    opacity: 1; }
  100% {
    left: calc(var(--left) + 40px);
    top: var(--top);
    transform: scale(0.2);
    opacity: 0; } }

@keyframes fly_icon {
  0% {
    transform: translateX(3px); }
  25% {
    transform: translateX(-3px); }
  50% {
    transform: translateX(3px); }
  75% {
    transform: translateX(-3px); }
  100% {
    transform: translateX(0px); } }

